.about {
    display: flex;
    flex-direction: row;
    min-height: 30rem;
    perspective: 10px;
  
    @media screen and (max-width: 768px) {
        flex-direction: column;
        gap: 1rem;
        padding-top: 1rem;
        min-height: auto;
    }
}

.about .left, 
.about .right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50%;
    padding: 1rem 1.5rem;
  
    @media screen and (max-width: 768px) {
        width: 100%;
        align-items: left;
        justify-content: left;
    }
}
